import React from 'react';
import { useTranslation } from 'react-i18next';

import { AddIcon, AddNewFolderContainer, AddNewFolderText, EllipsisContainer } from './styles';

const NewFolderRow = ({ inputText }: { inputText: string }): JSX.Element => {
  const { t: translation } = useTranslation();

  return (
    <AddNewFolderContainer>
      <AddIcon style={{ fontSize: 10 }} />
      <AddNewFolderText>
        {translation('newProfile.addNewFolder')} &nbsp;
      </AddNewFolderText>
      <span style={{ color: 'var(--000000-create-profile-folders-select)' }}>
        &quot;
        <EllipsisContainer>
          {inputText}
        </EllipsisContainer>
        &quot;
      </span>
    </AddNewFolderContainer>
  )
};

export default NewFolderRow;
