import styled from '@emotion/styled';
import { Radio, Button, Select } from 'antd';
import React, { FC, ReactElement, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { getUserOS, IOSParams, modifyUserAgentForWin11 } from '../../../utils/user-os';
import { macArmOptions } from '../../common/settings/browser/mac-arm-options';

const TabContainer = styled('div')`
  font-size: 12px;
  line-height: 20px;
  color: var(--000000-create-profile-overview);
`;

const ContentOperationSystem = styled('div')`
  margin-top: 30px;
  display: flex;
  flex-direction: column;
`;

const Subtitle = styled('span')`
  margin-bottom: 10px;
  display: block;
`;

const ButtonNewFingerprint = styled(Button)`
  font-size: 12px;
  margin-top: 10px;
  color: var(--1FC47D-create-profile-overview);
  border-color: var(--1FC47D-create-profile-overview);
  padding: 0 10px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: 'center;

  :hover {
    opacity: 0.7;
  }
`;

const RadioButton = styled(Radio.Button)`
  width: 90px;
  height: 30px;
  text-align: center;
  font-size: 12px;
  line-height: 29px;
  @media screen and (max-width: 325px) {
    width: 75px;
  }
`;

const RecommendedContainer = styled('div')`
  width: 450px;
  padding-left: 12px;
  padding-right: 12px;
  background-color: var(--F8F8F8-create-profile-overview);
  border-radius: 2px;
  margin-top: 10px;
`;

// any to be able to set type in implementation
const SelectAdvanced = styled<any>(Select)`
  width: 111px;
  margin-bottom: 10px;
  @media screen and (max-width: 450px) {
    width: 100%;
  }
`;

const RecommendedTxt = styled('div')`
  color: var(--00000099-create-profile-overview);
`;

declare interface IOverviewTab {
  updateProfileInfo: any;
  profileInfo: any;
  updateFingerprint: any;
  setUpdatedField: (field: string) => any;
}

const OverviewTab: FC<IOverviewTab> = (props) => {
  const { updateProfileInfo, profileInfo, updateFingerprint, setUpdatedField } = props;

  const [userOs, setUserOs] = useState<IOSParams>({ name: 'win' });

  const { t: translation } = useTranslation();

  useEffect(() => {
    getUserOS().then(setUserOs);
  }, []);

  const isElectron = !!window.require;

  const updateOsValue = async (event: any): Promise<void> => {
    const os = event.target.value;
    let targetOs = os;
    let { userAgent } = profileInfo.navigator;
    let osSpec = '';

    if (os === 'macArm') {
      targetOs = 'mac';
      osSpec = 'M1';
    }

    if (os === 'win11') {
      targetOs = 'win';
      osSpec = 'win11';
      userAgent = modifyUserAgentForWin11(userAgent);
    }

    await updateProfileInfo({
      os: targetOs,
      osSpec,
    });

    props.updateFingerprint();
    setUpdatedField('os');
    setUpdatedField('osSpec');
  };

  const getRadioValue = (): string => {
    if (profileInfo.os === 'mac' && profileInfo.osSpec?.includes('M')) {
      return 'macArm';
    }

    if (profileInfo.osSpec === 'win11') {
      return 'win11';
    }

    return profileInfo.os;
  };

  const getCompitableUserOs = (): string => {
    if (userOs.name === 'mac' && userOs.spec?.includes('M')) {
      return 'macArm';
    }

    if (userOs.spec === 'win11') {
      return 'win11';
    }

    return userOs.name;
  };

  const recommendedOsTable = (): string|null|void => {
    if (!isElectron && getRadioValue() !== 'lin') {
      return translation('newProfile.overview.recommendedOSTxt.web');
    }

    if (isElectron && userOs.name && getRadioValue() !== getCompitableUserOs()) {
      return translation('newProfile.overview.recommendedOSTxt.desktop');
    }
  };

  const handleOsChange = async (value: string): Promise<void> => {
    await updateProfileInfo({
      osSpec: value,
    });
    updateFingerprint();
    setUpdatedField('osSpec');
  };

  const getOsSpecSelector = (): JSX.Element | null => {
    if (getRadioValue() === 'macArm') {
      return (
        <div>
          <Subtitle style={{ marginTop: 20 }}>
            {translation('newProfile.overview.processorGeneration')}
          </Subtitle>
          <SelectAdvanced
            value={profileInfo.osSpec}
            onChange={handleOsChange}
          >
            {macArmOptions.map((option) => (
              <Select.Option value={option} key={option}>
                {option}
              </Select.Option>
            ))}

          </SelectAdvanced>
        </div>
      );
    }

    return null;
  };

  return (
    <TabContainer>
      <ContentOperationSystem>
        <Subtitle>
          {translation('base.operatingSystem')}
        </Subtitle>
        <Radio.Group
          value={getRadioValue()}
          size='default'
          onChange={updateOsValue}
        >
          <RadioButton value='win' style={{ width: 100 }}>
            Windows 10
          </RadioButton>
          <RadioButton value='win11' style={{ width: 100 }}>
            Windows 11
          </RadioButton>
          <RadioButton value='mac'>
            Mac Intel
          </RadioButton>
          <RadioButton value='macArm'>
            Mac ARM
          </RadioButton>
          <RadioButton value='lin'>
            Linux
          </RadioButton>
          <RadioButton value='android'>
            Android
          </RadioButton>
        </Radio.Group>

        {userOs.name ?
          <RecommendedContainer>
            <RecommendedTxt>
              {recommendedOsTable()}
            </RecommendedTxt>
          </RecommendedContainer> : null}
        {getOsSpecSelector()}
        <div>
          <ButtonNewFingerprint
            onClick={(): void => {
              updateFingerprint();
              setUpdatedField('fingerprint');
            }}
          >
            {translation('newProfile.overview.updateFingerprintBtnText')}
          </ButtonNewFingerprint>
        </div>
      </ContentOperationSystem>
    </TabContainer>
  );
};

export default OverviewTab;
