import { Select } from 'antd';
import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { browserUpdaterContext } from '../../../../state';
import { getSupportedOrbitaVersions } from '../../../common/api';
import { MajorVersionOption, normalizeOrbitaVersions } from '../../../common/orbita-browser';
import { addPrefix } from '../../../common/settings/browser/browser-version/utils';
import OrbitaVersionModals from '../../../modalsComponents/components/orbita-version-modals';

const BROWSER_VERSION_COMPATIBLE = '109';
const isElectron = !!window.require;
const ipcRenderer: Electron.IpcRenderer | null = isElectron ? window.require('electron').ipcRenderer : null;

interface IBrowserVersionNew {
  versionValue: MajorVersionOption;
  handleSelect: (value: MajorVersionOption) => void;
  isUpdateOrbitaModalVisible: boolean;
  setIsUpdateOrbitaModalVisible: (isVisible: boolean) => void;
  isOrbitaIncompatibleModalVisible: boolean;
  setIsOrbitaIncompatibleModalVisible: (isVisible: boolean) => void;
  downloadSpecificOrbitaVersion: (version: MajorVersionOption) => Promise<void>;
}

const BrowserVersionNew: React.FC<IBrowserVersionNew> = (props) => {
  const {
    versionValue,
    handleSelect,
    isUpdateOrbitaModalVisible,
    setIsUpdateOrbitaModalVisible,
    isOrbitaIncompatibleModalVisible,
    setIsOrbitaIncompatibleModalVisible,
    downloadSpecificOrbitaVersion,
  } = props;

  const [areButtonsDisabled, setAreButtonsDisabled] = useState(false);
  const [isCompatible, setIsCompatible] = useState(true);
  const [isDowngrading, setIsDowngrading] = useState(true);
  const [orbitaVersionsSupported, setOrbitaVersionsSupported] = useState<`${number}`[]>([]);

  const { t: translation } = useTranslation();
  const { browserUpdating, isOrbitaVersionCompatible } = useContext(browserUpdaterContext);

  const formatVersionLatest = (version: string): string =>
    `${translation('defaultProfileSettings.orbitaSelector.latest.title')} ${addPrefix(version)}`;

  const loadOrbitaVersions = async (): Promise<void> => {
    let { supportedOrbitaVersions } = await getSupportedOrbitaVersions();
    supportedOrbitaVersions = normalizeOrbitaVersions(supportedOrbitaVersions);
    setOrbitaVersionsSupported(supportedOrbitaVersions);
  };

  const checkOrbitaVersion = async (): Promise<void> => {
    if (browserUpdating || !ipcRenderer) {
      return;
    }

    let uaMajorVersion = versionValue;
    if (!versionValue || versionValue === 'unset') {
      uaMajorVersion = 'latest';
    }

    if (versionValue === '100') {
      uaMajorVersion = '10';
    }

    if (versionValue === '103') {
      uaMajorVersion = '13';
    }

    const { versionToDownload } = await ipcRenderer.invoke('check-supported-orbita', { key: 'orbitaMajorVersion', value: uaMajorVersion });
    if (versionToDownload) {
      setIsUpdateOrbitaModalVisible(true);
    }
  };

  const handleUpdateModalSubmit = (): void => {
    setIsUpdateOrbitaModalVisible(false);
    downloadSpecificOrbitaVersion(versionValue);
  };

  const handleIncompatibleSelectModalSubmit = (): void => {
    handleSelect(BROWSER_VERSION_COMPATIBLE);
    setIsOrbitaIncompatibleModalVisible(false);
  };

  const handleIncompatibleSelectModalClose = (): void => {
    if (!isCompatible) {
      handleSelect(BROWSER_VERSION_COMPATIBLE);
    }

    setIsOrbitaIncompatibleModalVisible(false);
  };

  const showModal = async (): Promise<void> => {
    const isVersionCompatible = await isOrbitaVersionCompatible(versionValue);
    setIsCompatible(isVersionCompatible);
    if (!isVersionCompatible) {
      setIsOrbitaIncompatibleModalVisible(true);
    }
  };

  const handleAfterClose = (): void => setAreButtonsDisabled(false);

  const getSelectValueNormalized = (): `${number}` => {
    let valueNormalized = versionValue || 'unset';
    if (valueNormalized === 'unset' || valueNormalized === 'latest') {
      [valueNormalized] = orbitaVersionsSupported;
    }

    return valueNormalized;
  };

  const shouldDowngradeOrbitaVersion = async (): Promise<boolean> => {
    if (versionValue !== 'unset') {
      return false;
    }

    const isVersionCompatible = await isOrbitaVersionCompatible(versionValue);

    return !isVersionCompatible;
  };

  const downgradeToVersionCompatible = async (): Promise<void> => {
    const shouldDowngrade = await shouldDowngradeOrbitaVersion();
    if (shouldDowngrade) {
      handleSelect(BROWSER_VERSION_COMPATIBLE);
    }
  };

  useEffect(() => {
    loadOrbitaVersions();
    if (isDowngrading) {
      downgradeToVersionCompatible().finally(() => setIsDowngrading(false));
    }
  }, []);

  useEffect(() => {
    if (isDowngrading) {
      return;
    }

    checkOrbitaVersion();
    showModal();
  }, [versionValue]);

  return (
    <>
      <Select
        value={getSelectValueNormalized()}
        onSelect={handleSelect}
        style={{ width: '175px' }}
      >
        {orbitaVersionsSupported.map((versionSupported, index) => (
          <Select.Option value={versionSupported} key={versionSupported}>
            {index ? addPrefix(versionSupported) : formatVersionLatest(versionSupported)}
          </Select.Option>
        ))}
      </Select>
      <OrbitaVersionModals.UpdateOrSkip
        modalType='orbitaUpdateOrSkip'
        visible={isUpdateOrbitaModalVisible}
        areButtonsDisabled={areButtonsDisabled}
        handleCancel={(): void => setIsUpdateOrbitaModalVisible(false)}
        handleSubmit={handleUpdateModalSubmit}
        handleAfterClose={handleAfterClose}
        orbitaVersion={getSelectValueNormalized()}
      />
      <OrbitaVersionModals.IncompatibleSelect
        visible={isOrbitaIncompatibleModalVisible}
        handleSubmit={handleIncompatibleSelectModalSubmit}
        handleCancel={(): void => setIsOrbitaIncompatibleModalVisible(false)}
        handleClose={handleIncompatibleSelectModalClose}
      />
    </>
  );
};

export default BrowserVersionNew;
