import styled from '@emotion/styled';
import { Button, Popconfirm, Table, Tooltip } from 'antd';
import React, { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';

import BodyContainer from './body-container';
import CookiesManagerProfile from './cookies-manager-profile';

export const Row = styled('div')`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const ButtonImportCookies = styled(Button)`
  font-size: 12px;
  margin-top: 8px;
  color: var(--1FC47D-create-profile-cookies);
  border-color: var(--1FC47D-create-profile-cookies);

  :hover {
    opacity: 0.7;
  }
`;

const ContainerNameCol = styled('div')`
  flex-direction: row;
  display: flex;
  align-items: baseline;
`;

const TableStyled = styled('div')`
  margin-bottom: 90px;
`;

const sortAlphabet = (a:any, b:any) => a > b ? 1 : a < b ? -1 : 0;

interface ICookiesTab {
  updateProfileInfo: (profileInfo: any) => any;
  cookies: any[];
  setUpdatedField: (field: string) => any;
}
const CookiesTab: FC<ICookiesTab> = (props) => {
  const [cookiesManagerModalVisible, setCookiesManagerModalVisible] = useState<boolean>(false);

  const { t: translation } = useTranslation();

  const showTable = () => (
    <ButtonImportCookies onClick={openCookiesManager}>
      {translation('newProfile.cookies.openImportDialogBtnText')}
    </ButtonImportCookies>
  );

  const closeCookiesManager = () => {
    setCookiesManagerModalVisible(false);
  };

  const openCookiesManager = () => {
    setCookiesManagerModalVisible(true);
  };

  const updateStateCookies = (cookies: any[]) => {
    props.updateProfileInfo({
      cookies,
    });

    props.setUpdatedField('cookies');
  };

  const clearCookies = () => {
    props.updateProfileInfo({
      cookies: [],
    });
    props.setUpdatedField('cookies');
  };

  const showButtonClearTop = () => (
    <Button
      type='danger'
      ghost={true}
      style={{
        backgroundColor: 'var(--B70000-create-profile-cookies)',
        color: 'var(--B70000-create-profile-cookies)',
        borderColor: 'var(--B70000-create-profile-cookies)',
        marginBottom: 20,
        height: 30,
        marginLeft: 13,
        paddingLeft: 13,
        fontSize: 12,
        borderRadius: 2,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      {translation('base.clear')}
    </Button>
  );

  const showButtonImportTop = () => (
    <Button
      onClick={openCookiesManager}
      type='danger'
      ghost={true}
      style={{
        backgroundColor: 'var(--1FC47D-create-profile-cookies)',
        color: 'var(--1FC47D-create-profile-cookies)',
        borderColor: 'var(--1FC47D-create-profile-cookies)',
        marginBottom: 20,
        height: 30,
        paddingLeft: 13,
        fontSize: 12,
        borderRadius: 2,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      {translation('base.import')}
    </Button>
  );

  const renderTableOrButton = () => {
    console.log(props.cookies.length);
    if (props.cookies.length) {
      return (
        <>
          <Row>
            {showButtonImportTop()}
            <Popconfirm
              placement='topLeft'
              title={translation('newProfile.cookies.clearConfirmationDialogText')}
              onConfirm={clearCookies}
              okText={translation('base.yes')}
              cancelText={translation('base.no')}
            >
              {showButtonClearTop()}
            </Popconfirm>
          </Row>

          <TableStyled>
            <Table
              bordered={true}
              pagination={false}
              rowKey='key'
              scroll={{ y: 500 }}
              locale={{ emptyText: showTable }}
              dataSource={props.cookies}
            >

              <Table.Column
                title={(
                  <ContainerNameCol>
                    <span>
                      {translation('base.name')}
                    </span>
                  </ContainerNameCol>
                )}
                dataIndex='name'
                key='name'
                sorter={(a: any, b: any) => sortAlphabet(a.name, b.name)}
                render={((value: string) => value.length > 18 ? <Tooltip title={value}>
                  {value.slice(0, 18)}
                  ...
                </Tooltip> : value)}
              />
              <Table.Column
                title={(
                  <ContainerNameCol>
                    <span>
                      {translation('base.value')}
                    </span>
                  </ContainerNameCol>
                )}
                dataIndex='value'
                key='value'
                sorter={(a: any, b: any) => sortAlphabet(a.value, b.value)}
                render={((value: string) => value.length > 18 ? <Tooltip title={value}>
                  {value.slice(0, 18)}
                  ...
                </Tooltip> : value)}
              />
              <Table.Column
                title={(
                  <ContainerNameCol>
                    <span>
                      {translation('base.expires')}
                    </span>
                  </ContainerNameCol>
                )}
                dataIndex='expirationDate'
                key='expirationDate'
                sorter={(a: any, b: any) => a.expirationDate - b.expirationDate}
              />
            </Table>
          </TableStyled>
        </>
      );
    }

    return showTable();
  };

  return (
    <>
      <BodyContainer>
        {renderTableOrButton()}
      </BodyContainer>

      <CookiesManagerProfile
        visible={cookiesManagerModalVisible}
        onClose={closeCookiesManager}
        importCookies={updateStateCookies}
      />
    </>
  );
};

export default CookiesTab;
