/* eslint-disable max-lines */
// Class component cannot work with destructured profileInfo from Redux
/* eslint-disable react/destructuring-assignment */
import styled from '@emotion/styled';
import { Button, Icon, Input, Tabs, Tooltip } from 'antd';
import moment from 'moment';
import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import { withTranslation, TransProps } from 'react-i18next';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';

import AdvancedTab from './advanced-tab';
import BodyContainer from './body-container';
import MainContainer from './container-dashboard';
import CookiesTab from './cookies-tab';
import FoldersSelect from './folders-select';
import GeolocationTab from './geolocation-tab';
import OverviewTab from './overview-tab';
import TimezoneTab from './timezone-tab';
import WebRTCTab from './webrtc-tab';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { IWorkspaceCtx, DEFAULT_PROFILE_NAME, templatesContext } from '../../../state';
import { getProxyRequest, setProxyStatuses } from '../../../state/proxies.context/api';
import { incrementProxyProfilesCount, updateProxyItem, updateProxyList, useProxyList } from '../../../state/proxy-list.atom';
import Header from '../../../ui/Header';
import { getAccessibleFolderName } from '../../../utils/check-folders-to-add-profile';
import { parseNameFormatting, updateTemplateProfileName } from '../../../utils/parse-name-formatting';
import { getParsedName } from '../../../utils/parse-name-formatting/get-parsed-name';
import { isProxyDataValid } from '../../../utils/proxy.validation-message';
import { getUserOS } from '../../../utils/user-os';
import { addNewProfile as addNewProfileAction } from '../../common/actions';
import { requestFingerprint } from '../../common/api';
import { randomWords } from '../../common/randomizer';
import BookmarksTab from '../../common/tabs/bookmarks';
import ExtensionsTab from '../../profileSettingsComponents/extensionsTab/extensions-tab';
import ProxyTab from '../../profileSettingsComponents/proxyTab';
import { IProxy } from '../../proxy/components/interfaces/IProxy';
import { getProxyStatusParams } from '../../proxy/proxy-helpers';
import { createProfile, createWorkspaceProfile, sendAnalytics, sendCookies } from '../api';
import { initialState } from '../reducer';

let ipcRenderer: Electron.IpcRenderer;
const isElectron = !!window.require;
if (isElectron) {
  ipcRenderer = window.require('electron').ipcRenderer;
}

const ContainerContent = styled('div')`
  margin: 30px 86px;
  color: var(--000000-create-profile);
  font-family: Roboto;
  font-size: 12px;
  @media screen and (max-width: 500px) {
    margin: 4% 5%;
  }
  @media screen and (max-width: 325px) {
    margin: 4% 2%;
  }
`;

const ColumnsContainer = styled('div')`
  display: flex;
  justify-content: space-between;
`;

const LeftColumnContainer = styled('div')`
  display: flex;
  flex-direction: column;
  @media screen and (max-width: 450px) {
    width: 100%;
  }
`;

const RightColumnContainer = styled('div')`
  max-width: 207px;
  display: flex;
  flex-direction: column;
  margin-bottom: 80px;
  @media screen and (max-width: 450px) {
    display: none;
  }
`;

const PageTitle = styled('h1')`
  margin: 0 !important;
  color: var(--000000-create-profile);
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
`;

const Subtitle = styled('span')`
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  margin-bottom: 10px;
  display: block;
`;

const ProfileSummaryContainer = styled('div')`
  background-color: var(--F6F6F6-create-profile);
  width: 207px;
`;

const ProfileSummaryContent = styled('div')`
  margin: 25px;
  display: flex;
  flex-direction: column;
  font-size: 11px;
  line-height: 15px;
  text-indent: 3px;
  opacity: 0.9;

  & span {
    padding: 5px 0;
  }
`;

const ContentBrowserName = styled('div')`
  width: 270px;
  margin-right: 20px;
`;

const ContentTabs = styled('div')`
  margin-top: 43px;
  width: 100%;
  max-width: 570px;
  @media screen and (max-width: 450px) {
    width: 100%;
  }
`;

const CreateButtonDiv = styled('div')`
  position: fixed;
  left: 0;
  width: 100%;
  bottom: 0;
  z-index: 999;
  padding: 1em;
  background-color: var(--FFFFFF-create-profile);
  box-shadow: 0 -4px 15px 0px var(--0000001A-create-profile);;
`;

const SaveAndNextBtn = styled(Button)`
  font-size: 12px;
  max-height: 30px;
  margin-top: 5px;
  margin-bottom: 7px;
  margin-left: 75px;
  text-shadow: none;
  @media screen and (max-width: 450px) {
    margin-left: 0;
  }
`;

const EllipsisContainer = styled('span')`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const GreenText = styled('span')`
  color: var(--2A5A2B-create-profile);
  margin-left: 4px;
`;

const updatedFields: any = {};

interface INewProfileProps extends TransProps {
  addNewProfile: any;
  profileInfo: any;
  history: any;
  proxyList: IProxy[];
  workspaceCtx: IWorkspaceCtx;
}

interface INewProfilePageState {
  defaultUserAgent: string;
  defaultLanguages: string;
  createBtnLoading: boolean;
  loadingPage: boolean;
  isGeolocationScreen: boolean;
  uaUserVersion: string;
  isFoldersSelectVisible: boolean;
}

class NewProfilePageClass extends Component<INewProfileProps, INewProfilePageState> {
  constructor(props: INewProfileProps) {
    super(props);

    this.state = {
      defaultUserAgent: '',
      defaultLanguages: '',
      createBtnLoading: false,
      loadingPage: true,
      isGeolocationScreen: false,
      uaUserVersion: '',
      isFoldersSelectVisible: false,
    };
  }

  public async componentDidMount(): Promise<void> {
    const { workspaceCtx, addNewProfile, profileInfo } = this.props;
    const { selectedTemplate } = this.context;
    const deviceOS = await getUserOS();

    const osParams = this.pickOsParams(deviceOS, selectedTemplate?.browser?.os, selectedTemplate?.browser?.osSpec);

    let selectedFolder = null;
    if (NEW_FEATURES.workspaces) {
      selectedFolder = getAccessibleFolderName({
        folders: workspaceCtx.folders,
        permission: 'addProfile',
        limitedAccess: !!workspaceCtx.me?.limitedAccess,
        hasFirstFolder: true,
        hasSelectedFolder: true,
      });
    }

    const { singleProfile, dropFiles } = selectedTemplate.profileName;
    const nameFormat = singleProfile || dropFiles || DEFAULT_PROFILE_NAME;
    const formattingParams = {
      profiles: [{}],
      nameFormat,
    };

    let name = '';
    if (NEW_FEATURES.templateProfileName) {
      [{ name = '' }] = parseNameFormatting(formattingParams);
      if (!name.trim()) {
        name = DEFAULT_PROFILE_NAME;
      }
    }

    await addNewProfile({
      ...selectedTemplate.browser,
      name,
      os: osParams.name,
      osSpec: osParams.spec,
      folders: selectedFolder ? [selectedFolder] : [],
    });

    await this.updateFingerprint();

    this.setState({
      defaultUserAgent: profileInfo.navigator.userAgent,
      defaultLanguages: profileInfo.navigator.language,
    });
  }

  private pickOsParams(deviceOsParams: { name: string; spec?: string }, templateOs: string, templateOsSpec: string): { name: string; spec?: string } {
    if (templateOs === 'deviceOS') {
      return deviceOsParams;
    }

    if (templateOs === 'macM1') {
      return {
        name: 'mac',
        spec: templateOsSpec || 'M1',
      };
    }

    return {
      name: templateOs,
      spec: templateOsSpec,
    };
  }

  static contextType = templatesContext;

  private updateUserVersionOrbita = (value: string): void => this.setState({ uaUserVersion: value });

  private setUpdatedField(field: string): void {
    updatedFields[field] = (updatedFields[field] || 0) + 1;
  }

  private setIsGeolocationScreen = (state: boolean): void => this.setState({ isGeolocationScreen: state });

  private updateFingerprint = async (): Promise<void> => {
    const templatesCtx = this.context;

    const { uaUserVersion } = this.state;
    const { profileInfo, addNewProfile } = this.props;
    const {
      autoLang,
      webRTC = {},
      webGL: defaultWebGL = {},
      webGLMetadata: defaultWebGLMetadata = {},
      canvas: defaultCanvas,
      fonts: defaultFonts = {},
      navigator: defaultNavigator = {},
      mediaDevices: defaultMediaDevices = {},
      plugins: defaultPlugins = {},
      timezone: defaultTimezone = {},
      webglParams: defaultWebglParams = {},
      clientRects: defaultClientRects = {},
      os = 'win',
      osSpec = '',
    } = profileInfo;

    const fingerprint =
      await requestFingerprint({ os, osSpec, template: templatesCtx.selectedTemplate.id, uauserversion: uaUserVersion })
        .catch(() => ({}));

    const {
      navigator,
      plugins,
      canvas,
      mediaDevices,
      webGLMetadata = {},
      webGL = {},
      webglParams = {},
      os: fprintOS,
      fonts,
      timezone = {},
      devicePixelRatio,
      clientRects = {},
      extensionsToNewProfiles = [],
      userExtensionsToNewProfiles = [],
    } = fingerprint;

    let deviceMemory = navigator.deviceMemory || 2;
    if (deviceMemory > 8) {
      deviceMemory = 8;
    }

    if (deviceMemory < 1) {
      deviceMemory = 1;
    }

    addNewProfile({
      fonts: {
        ...defaultFonts,
        families: fonts,
      },
      webGL: {
        ...defaultWebGL,
        mode:
          webGL.mode === 'noise' || webGL.mode === 'off'
            ? webGL.mode
            : defaultWebGL.mode,
      },
      clientRects: {
        ...defaultClientRects,
        mode:
          clientRects.mode === 'noise' || clientRects.mode === 'off'
            ? clientRects.mode
            : defaultClientRects.mode,
      },
      webGLMetadata: {
        ...defaultWebGLMetadata,
        mode:
          webGLMetadata.mode && webGLMetadata.mode === 'off'
            ? webGLMetadata.mode
            : 'mask',
        vendor: webGLMetadata.vendor || 'Google Inc.',
        renderer: webGLMetadata.renderer || '',
      },
      webRTC,
      mediaDevices: {
        ...defaultMediaDevices,
        audioInputs: mediaDevices.audioInputs || 0,
        audioOutputs: mediaDevices.audioOutputs || 0,
        videoInputs: mediaDevices.videoInputs || 0,
      },
      canvas: {
        ...defaultCanvas,
        mode:
          canvas.mode === 'noise' ||
          canvas.mode === 'off' ||
          canvas.mode === 'block'
            ? canvas.mode
            : defaultCanvas.mode,
      },
      plugins: {
        ...defaultPlugins,
        ...plugins,
      },
      autoLang,
      navigator: {
        ...defaultNavigator,
        ...navigator,
        deviceMemory,
      },
      timezone: {
        ...defaultTimezone,
        ...timezone,
      },
      webglParams: {
        ...defaultWebglParams,
        ...webglParams,
      },
      devicePixelRatio,
      chromeExtensions: extensionsToNewProfiles,
      chromeExtensionsToNewProfiles: extensionsToNewProfiles,
      userChromeExtensionsToNewProfiles: userExtensionsToNewProfiles,
      userChromeExtensions: userExtensionsToNewProfiles,
    });
    this.setState({
      loadingPage: false,
    });
  };

  private updateProfileName = (e: any) => {
    const { addNewProfile } = this.props;

    addNewProfile({
      name: e.target.value,
    });

    this.setUpdatedField('name');
  };

  private addNameToProfile = (profileName?: string): void => this.props.addNewProfile({
    name: profileName || randomWords(),
  });

  private getWebRtcMode = (enabled: boolean): string => {
    if (enabled) {
      return 'based on ip';
    }

    return 'off';
  };

  private saveProfile = async (): Promise<void> => {
    const {
      isGeolocationScreen,
      defaultUserAgent,
      defaultLanguages,
    } = this.state;

    const { t, workspaceCtx, proxyList, history } = this.props;

    const proxyDataForValidation = this.props.profileInfo.proxy;
    const isNeedValidationProxy =
      ['http', 'socks4', 'socks5'].includes(this.props.profileInfo.proxy.mode?.toLowerCase()) &&
      proxyDataForValidation.host &&
      proxyDataForValidation.port;

    if (isNeedValidationProxy) {
      const isProxyValid = isProxyDataValid({
        host: proxyDataForValidation.host,
        port: proxyDataForValidation.port,
        translation: t,
      });

      if (!isProxyValid) {
        return;
      }
    }

    this.setState({
      createBtnLoading: true,
    });

    if (
      isGeolocationScreen
      && (!(this.props.profileInfo.geoProxyInfo?.connection || this.props.profileInfo.proxy.host)
      || this.props.profileInfo.geoProxyInfo.isLoading)
    ) {
      return;
    }

    // if user create profile with empty custom proxy
    if (this.props.profileInfo.proxyEnabled &&
      (this.props.profileInfo.proxy.mode === 'none' ||
        (!this.props.profileInfo.proxy.host && !['gologin', 'tor', 'geolocation'].includes(this.props.profileInfo.proxy.mode))
      )
    ) {
      await this.props.addNewProfile({
        proxyEnabled: false,
      });
    }

    const templateCtx = this.context;
    const { profileName } = templateCtx.selectedTemplate;
    const { singleProfile = '', dropFiles = '' } = profileName;
    const nameFromTemplate = getParsedName(dropFiles, singleProfile);
    const { name = '' } = this.props.profileInfo;
    if (!name.trim()) {
      await this.addNameToProfile(nameFromTemplate);
    }

    const { userAgent = '' } = this.props.profileInfo.navigator;
    if (!userAgent.trim()) {
      await this.props.addNewProfile({
        navigator: {
          ...this.props.profileInfo.navigator,
          userAgent: defaultUserAgent,
        },
      });
    }

    if (!this.props.profileInfo.navigator.language.trim()) {
      await this.props.addNewProfile({
        navigator: {
          ...this.props.profileInfo.navigator,
          language: defaultLanguages,
        },
      });
    }

    if (this.props.profileInfo.proxy.mode === 'geolocation') {
      await this.props.addNewProfile({
        proxy: {
          ...this.props.profileInfo.proxy,
          changeIpUrl: '',
        },
      });
    }

    let newProfileData: any;
    try {
      if (NEW_FEATURES.workspaces) {
        const workspaceId = workspaceCtx.id;
        newProfileData = await createWorkspaceProfile(workspaceId, this.props.profileInfo);
      } else {
        newProfileData = await createProfile(this.props.profileInfo);
      }

      const needToUpdateTemplateProfileName = NEW_FEATURES.templateProfileName && dropFiles && nameFromTemplate === newProfileData.name;
      if (needToUpdateTemplateProfileName) {
        await updateTemplateProfileName(dropFiles, 1, templateCtx);
      }

      let resProxy: IProxy;
      const proxyInList = proxyList.find((el: IProxy) => el._id === newProfileData?.proxy?.id);

      if (!proxyInList && newProfileData?.proxy?.id) {
        const newProxy = await getProxyRequest(newProfileData.proxy.id);
        if (newProxy) {
          resProxy = newProxy;

          const newProxyList = [
            newProxy,
            ...proxyList,
          ];

          updateProxyList(newProxyList);
        }
      } else if (proxyInList) {
        resProxy = proxyInList;
        incrementProxyProfilesCount(proxyInList?._id);
      }

      if (resProxy?._id && newProfileData?.proxy?.id) {
        setTimeout(async (): Promise<void> => {
          updateProxyItem({
            ...resProxy,
            _id: resProxy._id,
            checkInProgress: true,
          });

          const statusParams = await getProxyStatusParams(resProxy);

          updateProxyItem({
            ...resProxy,
            ...statusParams,
            _id: resProxy._id,
            checkDate: moment().toDate(),
            checkInProgress: false,
          });

          const checkParams = {
            _id: resProxy._id,
            status: statusParams.status,
            country: statusParams.country || resProxy.country,
            city: statusParams.city || resProxy.city,
            error: statusParams.error || null,
            checkDate: moment().unix(),
            lastIp: statusParams.origin,
          };

          await setProxyStatuses([checkParams]).catch(() => null);
        }, 0);
      }
    } finally {
      this.setState({ createBtnLoading: false });
    }

    if (this.props.profileInfo.cookies.length) {
      await sendCookies(newProfileData.id, this.props.profileInfo.cookies);
    }

    // Sending fingerprint update fields analytics
    sendAnalytics(newProfileData.id, updatedFields);

    localStorage.removeItem('SelectedFolder');
    // Setting default values for state
    await this.props.addNewProfile(initialState);

    history.push('/');
  };

  private getAndroidTouchPoints = (): JSX.Element | null => {
    const { profileInfo, t: translation } = this.props;

    if (profileInfo.os !== 'android') {
      return null;
    }

    return (
      <span>
        {translation('base.maxTouchPoints')}
        :
        <GreenText>
          {profileInfo.navigator.maxTouchPoints}
        </GreenText>
      </span>
    );
  };

  private osHandler = (): string => {
    const { profileInfo } = this.props;

    return (profileInfo.os === 'mac' && profileInfo.isM1) ? 'mac M1' : profileInfo.os;
  };

  private updateProfileFolders = async (folders: string[]): Promise<void> => {
    await this.props.addNewProfile({ folders });
  };

  public render(): JSX.Element {
    const {
      createBtnLoading,
      loadingPage,
      isFoldersSelectVisible,
      uaUserVersion,
    } = this.state;

    const { t: translation, addNewProfile, profileInfo } = this.props;

    if (loadingPage) {
      return (
        <MainContainer>
          <Header />
          <BodyContainer>
            <ContainerContent>
              <ColumnsContainer>
                <LeftColumnContainer>
                  <PageTitle>
                    {translation('newProfile.title')}
                  </PageTitle>
                  <div style={{ fontSize: 32, marginTop: 20, color: 'var(--000000-create-profile-spinner)' }}>
                    <Icon type='loading' />
                  </div>
                </LeftColumnContainer>
              </ColumnsContainer>
            </ContainerContent>
          </BodyContainer>
        </MainContainer>
      );
    }

    return (
      <MainContainer onClick={(): void => this.setState({ isFoldersSelectVisible: false })}>
        <Header />
        <BodyContainer>
          <ContainerContent>
            <ColumnsContainer>
              <LeftColumnContainer>
                <PageTitle>
                  {translation('newProfile.title')}
                </PageTitle>
                <div style={{ display: 'flex', marginTop: 35 }}>
                  <ContentBrowserName>
                    <Input
                      size='small'
                      style={{ height: 32 }}
                      value={profileInfo.name}
                      onChange={this.updateProfileName}
                      placeholder={translation('newProfile.profileNameInputPlaceholder')}
                    />
                  </ContentBrowserName>
                  <FoldersSelect
                    selectedFolders={profileInfo.folders}
                    selectFolders={this.updateProfileFolders}
                    visible={isFoldersSelectVisible}
                    setVisible={(state: boolean): void => this.setState({ isFoldersSelectVisible: state })}
                  />
                </div>
                <ContentTabs>
                  <Tabs
                    defaultActiveKey='2'
                    size='small'
                  >
                    <Tabs.TabPane key='1' tab={translation('base.overview')} >
                      <OverviewTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        updateFingerprint={this.updateFingerprint}
                        setUpdatedField={this.setUpdatedField}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='2' tab={translation('proxy.title')}>
                      <ProxyTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                        isSaveButtonPressed={createBtnLoading}
                        isCreateProfile={true}
                        saveProfile={() => this.saveProfile()}
                        setIsGeolocationScreen={this.setIsGeolocationScreen}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='3' tab={translation('base.timezone')}>
                      <TimezoneTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='4' tab={translation('base.chromeExtensions')}>
                      <ExtensionsTab
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                        profileInfo={profileInfo}
                        isSaveButtonPressed={createBtnLoading}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='5' tab={translation('personalArea.defaultProfile.bookmarks')}>
                      <BookmarksTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField} />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='6' tab={translation('base.webrtc')}>
                      <WebRTCTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='7' tab={translation('geolocation.title')}>
                      <GeolocationTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='8' tab={translation('base.advanced')}>
                      <AdvancedTab
                        profileInfo={profileInfo}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                        uaUserVersion={uaUserVersion}
                        updateUserVersionOrbita={this.updateUserVersionOrbita}
                      />
                    </Tabs.TabPane>
                    <Tabs.TabPane key='9' tab={translation('base.cookies')}>
                      <CookiesTab
                        cookies={profileInfo.cookies}
                        updateProfileInfo={addNewProfile}
                        setUpdatedField={this.setUpdatedField}
                      />
                    </Tabs.TabPane>
                  </Tabs>
                </ContentTabs>
                <CreateButtonDiv>
                  <SaveAndNextBtn type='primary' onClick={() => this.saveProfile()} loading={createBtnLoading}>
                    {translation('newProfile.saveProfileBtnText')}
                  </SaveAndNextBtn>
                </CreateButtonDiv>
              </LeftColumnContainer>
              <RightColumnContainer>
                <Subtitle>
                  {translation('base.profileSummary')}
                </Subtitle>
                <ProfileSummaryContainer>
                  <ProfileSummaryContent>
                    <EllipsisContainer>
                      {translation('base.profileName')}
                      :
                      <GreenText>
                        {profileInfo.name}
                      </GreenText>
                    </EllipsisContainer>
                    <span>
                      {translation('proxy.title')}
                      :
                      <GreenText>
                        {profileInfo.proxy.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('newProfile.profileSummary.browser')}
                      :
                      <GreenText>
                        {profileInfo.browserType}
                      </GreenText>
                    </span>
                    <span>
                      {translation('base.os')}
                      :
                      <GreenText>
                        {this.osHandler()}
                      </GreenText>
                    </span>
                    <span>
                      {translation('base.useragent')}
                      :
                      <Tooltip
                        title={profileInfo.navigator.userAgent}
                      >
                        <GreenText>
                          {profileInfo.navigator.userAgent
                            .toString()
                            .slice(0, 8) + '...'}
                        </GreenText>
                      </Tooltip>
                    </span>
                    <span>
                      {translation('base.resolution')}
                      :
                      <GreenText>
                        {profileInfo.navigator.resolution}
                      </GreenText>
                    </span>
                    <span>
                      {translation('base.languages')}
                      :
                      <Tooltip
                        title={profileInfo.navigator.language}
                      >
                        <GreenText>
                          {profileInfo.navigator.language
                            .toString()
                            .slice(0, 10) + '...'}
                        </GreenText>
                      </Tooltip>
                    </span>
                    <span>
                      {translation('base.platform')}
                      :
                      <GreenText>
                        {profileInfo.navigator.platform}
                      </GreenText>
                    </span>
                    <EllipsisContainer>
                      {translation('base.timezone')}
                      :
                      <GreenText>
                        {
                          profileInfo.timezone.fillBasedOnIp
                            ? 'based on ip'
                            : profileInfo.timezone.timezone.length < 15
                              ? profileInfo.timezone.timezone
                              : <Tooltip title={profileInfo.timezone.timezone}>
                                {' '}
                                {profileInfo.timezone.timezone.slice(0, 15)}
                                ...
                                </Tooltip>
                        }
                      </GreenText>
                    </EllipsisContainer>
                    <span>
                      {translation('geolocation.title')}
                      :
                      <GreenText>
                        {profileInfo.geolocation.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('base.webrtc')}
                      :
                      <GreenText>
                        {this.getWebRtcMode(profileInfo.webRTC.enable)}
                      </GreenText>
                    </span>
                    <span>
                      {translation('canvas.title')}
                      :
                      <GreenText>
                        {profileInfo.canvas.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('clientRects.title')}
                      :
                      <GreenText>
                        {profileInfo.clientRects.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('webgl.metadata.title')}
                      :
                      <GreenText>
                        {profileInfo.webGLMetadata.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('webgl.image.title')}
                      :
                      <GreenText>
                        {profileInfo.webGL.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('audioContext.title')}
                      :
                      <GreenText>
                        {profileInfo.audioContext.mode}
                      </GreenText>
                    </span>
                    <span>
                      {translation('fonts.title')}
                      :
                      <GreenText>
                        {profileInfo.os === 'android' ? translation('fonts.androidNotifyText') : profileInfo.fonts.families.length}
                      </GreenText>
                    </span>
                    {this.getAndroidTouchPoints()}
                    <span>
                      {translation('mediaDevices.title')}
                      :
                      <Tooltip title="( videoIn | audioIn | audioOut )">
                        <GreenText>
                          (
                          {profileInfo.mediaDevices.videoInputs}
                          |
                          {profileInfo.mediaDevices.audioInputs}
                          |
                          {profileInfo.mediaDevices.audioOutputs}
                          )
                        </GreenText>
                      </Tooltip>
                    </span>
                    <span>
                      {translation('base.localStorage')}
                      :
                      <GreenText>
                        {profileInfo.storage.local.toString()}
                      </GreenText>
                    </span>
                    <span>
                      {translation('newProfile.profileSummary.extStorage')}
                      :
                      <GreenText>
                        {profileInfo.storage.extensions.toString()}
                      </GreenText>
                    </span>
                    <span>
                      {translation('base.plugins')}
                      :
                      <GreenText>
                        {profileInfo.plugins.enableVulnerable.toString()}
                      </GreenText>
                    </span>
                  </ProfileSummaryContent>
                </ProfileSummaryContainer>
              </RightColumnContainer>
            </ColumnsContainer>
          </ContainerContent>
        </BodyContainer>
        <Helmet>
          {/* @ts-ignore */}
          <title>
            {translation('newProfile.title')}
            {' '}
            GoLogin
            {' '}
            {window.gologinAppVersion || ''}
          </title>
        </Helmet>
      </MainContainer>
    );
  }
}

const mapStateToProps = (state: any): { profileInfo: unknown } => ({
  profileInfo: state.newProfile,
});

const mapDispatchToProps = {
  addNewProfile: addNewProfileAction,
};

const NewProfilePageClassComponent = withRouter(
  connect(mapStateToProps, mapDispatchToProps)(withTranslation()(NewProfilePageClass)),
);

// eslint-disable-next-line react/no-multi-comp
export const NewProfilePage: React.FC = (props)  => {
  const proxyList = useProxyList();

  return (<NewProfilePageClassComponent
    proxyList={proxyList}
    workspaceCtx={props.workspaceCtx}
  />);
};

export default NewProfilePage;
