import { FolderOutlined } from '@material-ui/icons';
import { Dropdown, message } from 'antd';
import React, { FC, useContext, useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { getFoldersList } from './api';
import Menu from './menu';
import {
  Container,
  FolderChip,
  InputElem,
} from './styles';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { IFolder } from '../../../../interfaces';
import { userContext, workspaceContext } from '../../../../state';
import { addFolder, createWorkspaceFolder } from '../../../folders/api';

interface IFoldersSelect {
  selectedFolders: string[];
  selectFolders: (newFoldersList: string[]) => void;
  visible: boolean;
  setVisible: (state: boolean) => void;
}

const FoldersSelect: FC<IFoldersSelect> = (props) => {
  const [inputText, setInputText] = useState<string>('');
  const [marginTopDropdown, setMarginTopDropdown] = useState<number>(0);
  const [foldersList, setFoldersList] = useState<IFolder[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const {
    folders: foldersListFromCtx,
    updateFolders,
  } = useContext(userContext);

  const foldersListWhenToggle = NEW_FEATURES.header ? foldersListFromCtx : foldersList;

  const foldersRef = useRef<HTMLDivElement>(null);

  const { id: workspaceId } = useContext(workspaceContext);

  const { t: translation } = useTranslation();

  const { selectedFolders, selectFolders, visible, setVisible } = props;

  const getFolders = async (): Promise<void> => {
    const folders = await getFoldersList();
    folders.sort((a, b) => a.order - b.order);

    setFoldersList(folders);
    setIsLoading(false);
  };

  useEffect(() => {
    if (NEW_FEATURES.header) {
      setIsLoading(false);

      return;
    }

    getFolders();
  }, []);

  useEffect(() => {
    setMarginTopDropdown(foldersRef.current?.clientHeight || 0);
  }, [selectedFolders.length]);

  const createNewFolder = async (): Promise<void> => {
    const inputFolderName = inputText.replace(/\s+/g, ' ').trim();

    if (!inputFolderName) {
      message.error(translation('notifications.error.emptyFolder'));

      return;
    }

    const existingFolder = foldersListWhenToggle.find(folder => folder.name === inputFolderName);
    if (existingFolder) {
      message.error(`Folder "${inputFolderName}" already exists`);

      return;
    }

    setIsLoading(true);

    let createdFolder;
    if (NEW_FEATURES.workspaces) {
      createdFolder = await createWorkspaceFolder(workspaceId, inputFolderName);
    } else {
      createdFolder = await addFolder(inputFolderName, []);
    }

    if (NEW_FEATURES.header) {
      const newFoldersList = foldersListFromCtx;
      newFoldersList.push({ name: createdFolder.name, order: newFoldersList.length, id: createdFolder.id });
      localStorage.setItem('folders-order', JSON.stringify(newFoldersList));
      updateFolders(newFoldersList);
      toggleSelectedFolder(inputFolderName);
      setInputText('');
      setIsLoading(false);

      return;
    }

    const rawCachedFolders = localStorage.getItem('folders-order');
    const newFoldersList = rawCachedFolders ? JSON.parse(rawCachedFolders) : foldersList;
    newFoldersList.push({ name: createdFolder.name, order: newFoldersList.length, id: createdFolder.id });
    localStorage.setItem('folders-order', JSON.stringify(newFoldersList));

    toggleSelectedFolder(inputFolderName);
    setInputText('');
    setFoldersList(newFoldersList);
    setIsLoading(false);
  };

  const toggleSelectedFolder = (folderName: string): void => {
    const hasFolder = selectedFolders.find(f => f === folderName);
    const newSelectedFolders = hasFolder ? selectedFolders.filter(f => f !== folderName) : [...selectedFolders, folderName];

    selectFolders(newSelectedFolders);
  };

  const onInputKeyDown = (event: React.KeyboardEvent<HTMLInputElement>): void => {
    if (!visible) {
      setVisible(true);
    }

    if (event.key === 'Escape') {
      setVisible(false);

      return;
    }

    if (event.key !== 'Enter') {
      return;
    }

    event.preventDefault();
    createNewFolder();
  };

  const renderSelectedFolders = (): JSX.Element => (
    <>
      {selectedFolders.map(f => (
        <FolderChip
          icon={<FolderOutlined viewBox={'0 0 36 24'} />}
          label={f}
          onDelete={(): void => toggleSelectedFolder(f)}
          key={f}
          deleteIcon={<span style={{ marginLeft: 5 }}></span>}
        />
      ))}
    </>
  );

  const menuProps = {
    isLoading,
    foldersList: foldersListWhenToggle,
    selectedFolders,
    toggleSelectedFolder,
    inputText,
    createNewFolder,
  };

  return (
    <div
      onClick={(e): void => {
        e.stopPropagation();
        setVisible(true);
      }}
    >
      <Dropdown
        overlayStyle={{
          zIndex: 999,
          boxShadow: '0px 4px 5px 1px var(--0000001A-create-profile-folders-select)',
          borderRadius: 4,
          marginTop: marginTopDropdown,
        }}
        overlay={<Menu {...menuProps} />}
        trigger={['click']}
        visible={visible}
      >
        <Container isFocused={visible} ref={foldersRef}>
          {renderSelectedFolders()}
          <InputElem
            placeholder={!selectedFolders.length ? translation('base.noFolder') : ''}
            value={inputText}
            onChange={(e): void => setInputText(e.target.value)}
            onKeyDown={onInputKeyDown}
          />
        </Container>
      </Dropdown>
    </div>
  );
};

export default FoldersSelect;
