import { Button, message, Modal } from 'antd';
import React, { DragEvent, FC, useState, MouseEvent, ChangeEvent } from 'react';
import { useTranslation } from 'react-i18next';

import IconCookiesDrop from '../../../ui/icons/IconCookiesDrop';
import { getFormattedCookies } from '../../../utils/cookies-parser';
import {
  CookiesTextArea,
  DEFAULT_COOKIES_BORDER_COLOR,
  DrugAndDropCookiesContainer,
  JsonTitleTxt,
} from '../../modalsComponents/components/cookies-manager/styles/styles';
import CookiesManagerModal from '../../modalsComponents/components/cookies-manager-modal';


interface ICookiesManagerProfile {
  visible: boolean;
  onClose: () => any;
  importCookies: (cookies: any) => any;
}

const CookiesManagerProfile: FC<ICookiesManagerProfile> = (props) => {
  const [cookiesTxt, setCookiesTxt] = useState<string>('');
  const [cookiesBorderColor, setCookiesBorderColor] = useState<string>(DEFAULT_COOKIES_BORDER_COLOR);
  const [cookiesButtonDisabled, setCookiesButtonDisabled] = useState<boolean>(true);

  const { visible, onClose, importCookies } = props;

  const { t: translation } = useTranslation();

  const [placeholder, setPlaceholder] = useState<string>(translation('newProfile.cookies.pasteText') || '');

  const onDragOver = (event: MouseEvent): void => {
    event.stopPropagation();
    event.preventDefault();
  };

  const parseFile = (files: FileList): void => {
    for (const file of files) {
      const allowedFileType = file.type === 'application/json' || file.type.match(/^text*/);
      if (!allowedFileType) {
        message.error(translation('notifications.error.onlyTextFile'));

        return;
      }

      const reader: any = new FileReader();
      reader.readAsText(file);

      reader.onload = (): void => {
        let isCookiesButtonDisabled = true;
        let cookiesBorderCurrentColor = 'red';
        try {
          getFormattedCookies(reader.result);
          isCookiesButtonDisabled = false;
          cookiesBorderCurrentColor = DEFAULT_COOKIES_BORDER_COLOR;
        } catch (e) {}

        setCookiesTxt(reader.result);
        setCookiesButtonDisabled(isCookiesButtonDisabled);
        setCookiesBorderColor(cookiesBorderCurrentColor);
      };

      reader.onerror = (): void => {
        message.error(reader.error);
      };
    }
  };

  const closeCookiesModal = (): void => {
    setCookiesTxt('');
    setCookiesBorderColor(DEFAULT_COOKIES_BORDER_COLOR);
    onClose();
  };

  const hiddenFileInput = React.useRef<HTMLInputElement>(null);

  const handleUploadClick = (): void => {
    if (hiddenFileInput.current) {
      hiddenFileInput.current.value = '';
    }

    hiddenFileInput && hiddenFileInput.current?.click();
  };

  const handleDrop = (event: DragEvent): void => {
    event.preventDefault();
    event.stopPropagation();
    const { files } = event.dataTransfer;
    parseFile(files);
  };

  const handleUploadChange = (event: any): void => {
    event.preventDefault();
    event.stopPropagation();
    const filesUploaded = event.target.files;
    parseFile(filesUploaded);
  };

  const changeCookiesTxt = (event: ChangeEvent<HTMLTextAreaElement>): void => {
    const txt = event.target.value;
    let isCookiesButtonDisabled = true;
    let cookiesBorderCurrentColor = 'red';

    try {
      getFormattedCookies(txt);
      isCookiesButtonDisabled = false;
      cookiesBorderCurrentColor = DEFAULT_COOKIES_BORDER_COLOR;
    } catch (err) {}

    setCookiesTxt(event.target.value);
    setCookiesButtonDisabled(isCookiesButtonDisabled);
    setCookiesBorderColor(cookiesBorderCurrentColor);
  };

  const cookiesSettings = (): JSX.Element => (
    <div>
      <p>
        {translation('cookieManagerModal.subtitle')}
        &nbsp;
        <JsonTitleTxt
          onMouseEnter={(): void => setPlaceholder('[{ "key": "value" }, {...}]')}
          onMouseLeave={(): void => setPlaceholder(translation('newProfile.cookies.pasteText') || '')}
        >
          {translation('base.json')}
        </JsonTitleTxt>
        &nbsp;
        {translation('base.and')}
        &nbsp;
        <JsonTitleTxt
          onMouseEnter={(): void => setPlaceholder('domain.com\tTRUE\t/path\tTRUE\texpirationDate\tname\tvalue\n...')}
          onMouseLeave={(): void => setPlaceholder(translation('newProfile.cookies.pasteText') || '')}
        >
          {translation('base.netscape')}
        </JsonTitleTxt>
        &nbsp;
        {translation('base.format')}
      </p>
      <DrugAndDropCookiesContainer
        onDrop={handleDrop}
        onDragOver={onDragOver}
        onClick={handleUploadClick}
      >
        <input
          type='file'
          ref={hiddenFileInput}
          onChange={handleUploadChange}
          style={{ display:'none' }}
        />
        <IconCookiesDrop />
      </DrugAndDropCookiesContainer>
      <div style={{ textAlign: 'center' }}>
        <h3 style={{ color: DEFAULT_COOKIES_BORDER_COLOR }}>
          {translation('base.or')}
        </h3>
      </div>
      <CookiesTextArea
        value={cookiesTxt}
        onChange={changeCookiesTxt}
        style={{ borderColor: cookiesBorderColor }}
        placeholder={placeholder}
        onDrop={handleUploadChange}
      />
    </div>
  );

  const saveCookies = (): void => {
    const formattedCookies = getFormattedCookies(cookiesTxt);
    console.log('formattedCookies:', formattedCookies);
    importCookies(formattedCookies);

    closeCookiesModal();
  };

  return (
    <CookiesManagerModal
      visible={visible}
      cookiesButtonDisabled={cookiesButtonDisabled}
      saveCookies={saveCookies}
      closeCookiesModal={closeCookiesModal}
      cookiesSettings={cookiesSettings}
    />
  );
};

export default CookiesManagerProfile;
