import { FolderOutlined } from '@material-ui/icons';
import { Icon } from 'antd';
import React, { FC } from 'react';

import { IFolder } from './folder.interface';
import NewFolderRow from './new-folder-row';
import { searchBold } from './search';
import { DoneIcon, DropdownMenu, FolderNameContainer, FolderNameText, MenuItem } from './styles';

interface IMenu {
  isLoading: boolean;
  foldersList: IFolder[];
  selectedFolders: string[];
  toggleSelectedFolder: (folderName: string) => void;
  inputText: string;
  createNewFolder: () => void;
}

const Menu: FC<IMenu> = (props): JSX.Element => {
  const {
    isLoading,
    foldersList,
    selectedFolders,
    toggleSelectedFolder,
    inputText,
    createNewFolder,
  } = props;

  const filteredFolders = !inputText ? foldersList : foldersList.filter(f => f.name.toLowerCase()?.includes(inputText.toLowerCase()));

  const renderNewFolderRow = (): JSX.Element | null => {
    if (!inputText || foldersList.map(f => f.name).includes(inputText)) {
      return null;
    }

    return (
      <DropdownMenu>
        <MenuItem style={{ height: 32, padding: '8px 16px 10px', margin: 0 }} onClick={createNewFolder}>
          <NewFolderRow inputText={inputText} />
        </MenuItem>
      </DropdownMenu>
    );
  };

  if (isLoading) {
    return (
      <div style={{ fontSize: 32, textAlign: 'center' }}>
        <Icon type='loading' />
      </div>
    );
  }

  return (
    <>
      <DropdownMenu style={{ padding: '8px 0 0' }}>
        {filteredFolders.map(f => (
          <MenuItem
            style={{ height: 32, padding: '0px 0 0 14px', margin: 0 }}
            onClick={(): void => toggleSelectedFolder(f.name)}
            key={f.name}
          >
            <FolderNameContainer>
              <FolderOutlined viewBox={'0 0 36 24'} />
              <FolderNameText>
                {searchBold(inputText, f.name)}
              </FolderNameText>
            </FolderNameContainer>
            {selectedFolders.includes(f.name) ? <DoneIcon viewBox={'1 0 36 32'} /> : null}
          </MenuItem>
        ))}
      </DropdownMenu>
      {renderNewFolderRow()}
    </>
  );
};

export default Menu;
