import { v4 as uuidv4 } from 'uuid';

import { API_BASE_URL } from '../../../common/constants/constants';
import { initGA } from '../../initGA';
import { IProfile } from '../../interfaces';
import { http } from '../../services';
import { handleProxiesLimitError } from '../proxy/utils/handle-proxies-limit-error';

export const createProfile = async (profileData: any): Promise<string> => {
  const googleClientId = await initGA();

  const data = await http(`${API_BASE_URL}/browser`, {
    method: 'POST',
    body: JSON.stringify({
      ...profileData,
      newStartupUrlLogic: true,
      googleClientId,
    }),
  });

  return data.body;
};

export const createWorkspaceProfile = async (workspaceId: string, profileData: any): Promise<IProfile> => {
  const googleClientId = await initGA();

  const folder = profileData.folder ? `?folder=${profileData.folder}` : '';

  return http(`${API_BASE_URL}/workspaces/${workspaceId}/profiles/old${folder}`, {
    method: 'POST',
    body: JSON.stringify({
      ...profileData,
      newStartupUrlLogic: true,
      googleClientId,
    }),
  }).then(({ body }: { body: IProfile }) => body).catch(handleProxiesLimitError);
};

export const sendCookies = async (profileId: string, cookies: any): Promise<string> => {
  const bodyParams = JSON.stringify(cookies);

  const data = await http(`${API_BASE_URL}/browser/${profileId}/cookies?fromUser=true`, {
    method: 'POST',
    body: bodyParams,
  });

  return data.body;
};

export const getProxy = async (): Promise<string> => {
  const url = `${API_BASE_URL}/users-proxies/profiles-proxies`;

  const data = await http(url, {
    method: 'GET',
  });

  return data.body;
};

export const sendAnalytics = (profileId: string, updatedFields: any): Promise<string> => {
  const body = Object.keys(updatedFields).map((key: string) => ({
    field: key,
    count: updatedFields.key,
    profile: profileId,
    type: 'fingerprint-update',
    status: 'success',
    deduplicationKey: uuidv4(),
  }));

  return http(`${API_BASE_URL}/analytics/many`, {
    method: 'POST',
    body: JSON.stringify({
      analytics: body,
    }),
  }).catch(() => Promise.resolve());
};

export const getVersionOrbitaByMajor = async (version: string): Promise<string> => {
  const data = await http(`${API_BASE_URL}/gologin-global-settings/orbita_version_by_major?orbita_major_version=${version}`);

  return data.body;
};
